@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu {
    height: 40px;
}

.general {
    background-color: #292E49;
    width: 100%;
    margin-bottom: 10px;
}

.landing-style {
    background-color: transparent;
    width: 100%;
    margin-bottom: 10px;
}

.landing-style .general li {
    width: 100px;
}

.landing-style li a, .general li a {
    color: white;
    text-align: center;
}

.landing-style .pure-menu-active>.pure-menu-link, .pure-menu-link:hover, .pure-menu-link:focus, .general .pure-menu-active>.pure-menu-link, .pure-menu-link:hover, .pure-menu-link:focus {
    background-color: transparent;
}

.landing-style li a:hover, .general li a:hover {
    background-color: none;
}

.landing-style li::before, .general li::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: white;
    visibility: hidden;
    transform: scaleX(0);
    transition: all .35s ease-in-out;
}

.landing-style li:hover::before, .general li:hover::before {
    visibility: visible;
    transform: scaleX(1);
}

@media only screen and (min-width: 600px) {
    .navbar-brand {
        margin-right: 8.5rem;
    }
}

@media only screen and (max-width: 600px) and (min-width: 450px) {
    .navbar-brand {
        margin-right: -1em;
    }
}

@media only screen and (max-width: 450px) {
    .navbar-brand {
        margin-right: -1em;
    }
    #logo {
        width:80%;
    }
}

.custom-switch {
    margin-top: 5px;
}
.custom-control-label {
    color: white;
}

#input-group-dropdown-2 {
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
}
html, body {
    background-color: white;
    width: 100%;
    height: 100%;
    margin: 0;
}

.content {
    font-family: 'Open Sans', serif;
    font-size: 1rem;
    padding: 1.5em;
    margin-left: 2em;
    margin-right: 2em;
}

.search-style {
    width: 100%;
}

@media only screen and (max-width: 800px) {
    /* For mobile phones: */
    .content {
        padding: 1em;
        margin-left: 0.5em;
        margin-right: 0.5em;
    }

    .l-box {
        padding: 0;
    }
}

@media only screen and (min-width: 2000px) {
    /* For mobile phones: */
    .content {
        padding: 1em;
        margin-left: 15em;
        margin-right: 15em;
    }

    .l-box {
        padding: 1em;
    }
}

.badge {
    float:right;
    margin-top: 3px;
}
.component-wrapper {
    background-color: whitesmoke;
    padding: 0.2em;
}

.publisher {
    display:flex;
    align-content: center;
    padding: 0.2em;
    font-size: small;
    color: grey;
}

@media only screen and (max-width: 600px) {
    .last-row {
      margin-bottom: 15%;
      margin-top: 2%;
    }
  }
.version {
    margin: 0
}
.grid-item-wrapper{
    padding: 0em;
}

.grid-item {
    margin: 10px;
}

@media only screen and (min-width: 2000px) {
    /* For mobile phones: */
    .grid-item-wrapper{
        padding: 1.5em;
        padding-left: 0;
    }
    
}
#react-paginate ul {
    display: inline-block;
    padding-left: 15px;
    padding-right: 15px;
}

#react-paginate li {
    display: inline-block;
}

#react-paginate a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: #ddd .3s;
    border: 1px solid #ddd;
  }
    
  #react-paginate a:hover:not(.active) {background-color: #ddd;}
/*This removes right margin from ul list */

ul.package-list  {
    -webkit-padding-start: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
}

.active {
    background-color: rgba(0,123,255,0.4);
}
.menuScroll {
    overflow-y: auto;
    max-height: 300px;
}
.verifiedFilter {
    margin-left: auto;
}


@media only screen and (max-width: 600px) {
    .dropdown-button {
        margin-bottom: 10px;
    }
    .verifiedFilter {
        margin-left: 0;
        margin-bottom: 10px;
    }
  }

